<template>
  <div class="h-full w-full back-color pb-64">
    <div class="container mx-auto text-center pt-10 px-6">
      <div class="grid-cols-1 text-left pb-12">
        <a @click.prevent="backRoute" class="text-white text-2xl"><i class="fas fa-arrow-left"></i></a>
      </div>
      <div class="grid-cols-1 text-white text-left text-2xl pb-8">
        <h2>Добавтье ваше фото и имя {{ info.info.email }}</h2>
      </div>
      <div class="grid grid-cols-6 gap-4 bg-white rounded-lg py-4">
          <div class="col-span-2 flex mx-auto">
            <img class="center-img w-20 h-20 rounded-full bg-gray-500" :src="selectedFile" alt="">
          </div>
          <div class="col-span-4 pt-3 mr-2">
            <input v-model="name" placeholder="Ваше имя" type="text" class="placeholder-white focus:placeholder-gray-500  w-full font-semibold focus:outline-none text-white focus:text-gray-800 focus:bg-white border-b-2 focus:border-gray-500 bg-gray-500 h-10 px-4 rounded-full">
            <p v-if="$v.name.$dirty && !$v.name.required" class="text-red-500 text-xs italic pt-1">Пожалуста введите ваше имя</p>
          </div>
      </div>
      <label for="file-input" class="focus:outline-none mx-auto block text-center pt-3 bg-green-600 font-semibold h-12 w-48 rounded-full mt-8 mb-8 text-white">Добавить фото</label>
      
      <input type="file" id="file-input" class="img-input" @change="onFileSelected">
      <button @click.prevent="nextPage" @click="onUpload" class="mx-auto block focus:outline-none bg-blue-500 font-semibold h-12 w-48 rounded-full mt-4 mb-8 text-white">Дальше</button>
    </div>
  </div>
</template>
<script>
import firebase from 'firebase'
import { required } from 'vuelidate/lib/validators'

export default {
  metaInfo: {
    title: `User`,
  },
  data: () => ({
    selectedFile: null,
    name: '',
    uid: '',
    

    
  }),
  async mounted() {
    this.info = await this.$store.dispatch('fetchInfo');
    this.uid = await this.$store.dispatch('getUid');
    this.selectedFile = this.info.profile.photo
    this.name = this.info.profile.name
  },
  validations: {
    name: {
      required,
    }
  },
  methods: {
    async onUpload() {
      if(this.$v.$invalid) {
        this.$v.$touch()
        return
      }
      const userInfo = {
        name: this.name,
        photo: this.selectedFile

      }
      await this.$store.dispatch('updatePhoto', userInfo);
      
    },
    onFileSelected(event) {
      const file = event.target.files[0]
      var storageRef = firebase.storage().ref(`users/${this.uid}/` + file.name) ;
      let uploadTask = storageRef.put(file);
      uploadTask.on('state_changed', (snapshot) =>{
        console.log(snapshot)
      }, (error) => {
        console.log(error)
      }, () => {
        uploadTask.snapshot.ref.getDownloadURL().then((downloadURL) => {
          this.selectedFile = downloadURL
          console.log(downloadURL)
        })
      })
    },
    nextPage() {
      this.$router.push({ name: 'UserInfo' })
    },
    backRoute() {
      this.$router.go(-1)
    },

      
  },
  computed: {
    info() {
      return this.$store.getters.info
    }
  },
  
  
}
</script>
<style scoped>
  .img-input {
    display: none;
  }
  
  .back-color {
    background-color: #000000;
    background-image: linear-gradient(315deg, #000000 0%, #111111 74%);
    height: auto !important;
    min-height: 100%;
    height: 100%;
  }
  .center-image {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
  }
  
</style>